$(document).ready(function () {
	$('.slider').slick({
		arrows: true,
		dots: false,
		slidesToShow: 1,
		fade: true,
		adaptiveHeight: true,
		autoplay: false,
		speed: 1000,
		autoplaySpeed: 800,
		responsive: [
			{
				breakpoint: 768,
				settings: {
					slidesToShow: 1
				}
			},
			{
				breakpoint: 550,
				settings: {
					slidesToShow: 1
				}
			}
		]
	});
});

